let image = document.querySelector('.slider-bunner__image');

let count = 0;

function showImage() {
    if (count != 6) {
        count += 1;
        image.src = `./img/${count}_bunner.jpg`;

    } else {
        count = 0;
    }
}
// let timer = setInterval(showImage, 14000);

export default showImage;